<template>
    <div class="worksModalContainer">
        <v-dialog v-model="show"
                  max-width="600px"
                  persistent
        >
            <template v-slot:activator="{ on, attr }">
                <v-btn class="mx-sm-4" v-bind="attr" v-on="on" @click="add">
                    <v-icon>mdi-plus</v-icon>
                    <span>הוסף עבודה חדשה</span>
                </v-btn>
            </template>
            <v-card>
                <v-card-title>
                    <span class="headline">הוסף עבודה חדשה</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <date-input></date-input>
                            <company-inputs></company-inputs>
                            <work-time-inputs></work-time-inputs>
                            <work-description-input></work-description-input>

                            <v-col cols="12" sm="1">
                                <v-checkbox v-model="workCard.paid"
                                ></v-checkbox>
                            </v-col>
                            <v-col cols="12" sm="5">
                                <v-text-field label="* שולם"
                                              v-model="workCard.paidSum"
                                              required
                                ></v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6">
                                <v-autocomplete label="אמצעי תשלום"
                                                single
                                                v-model="workCard.paymentInst"
                                                :items="paymentSelectItems"
                                ></v-autocomplete>
                            </v-col>
                            <v-col cols="12">
                                <v-text-field label="הערות"
                                              v-model="workCard.notes"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                    <small>* שדות חובה</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="blue darken-1"
                            text
                            @click="show = false"
                            v-text="'Close'"
                    ></v-btn>
                    <v-btn
                            color="blue darken-1"
                            text
                            @click="save"
                            v-text="'Save'"
                    ></v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import {mapActions, mapState} from 'vuex';
    // import NewWorkButton from './NewWorkButton'
    import DateInput from './DateInput'
    import CompanyInputs from './CompanyInputs'
    import WorkTimeInputs from './WorkTimeInputs'
    import WorkDescriptionInput from './WorkDescriptionInput'

    export default {
        name : "modal",
        props : {
            value : {
                type : Boolean,
                default : false
            },
        },
        data() {
            return {
                paymentSelectItems : [
                    {
                        value : "CHECK",
                        text : "צ'ק"
                    },
                    {
                        value : "CASH",
                        text : "מזומן"
                    },
                    {
                        value : "CREDIT_CARD",
                        text : "כרטיס אשראי"
                    },
                    {
                        value : "REMITTANCE",
                        text : "העברה בנקאית"
                    }
                ]
            }
        },
        components : {
            // NewWorkButton,
            WorkDescriptionInput,
            WorkTimeInputs,
            CompanyInputs,
            DateInput,
        },
        computed : {
            ...mapState({
                worksData : state => state.works.worksData,
                modalWorkCard : state => state.works.modalWorkCard,
            }),
            workCard() {
                return this.modalWorkCard;
            },
            show : {
                get() {
                    return this.value
                },
                set(value) {
                    this.$emit('input', value)
                }
            }
        },
        methods : {
            ...mapActions({
                addWork : 'works/newWork',
                saveWork : 'works/updateWork'
            }),
            add() {
                this.addWork(this.workCard);
                this.show = true;
            },
            save() {
                console.log('save function');
                this.saveWork(this.workCard);
                this.show = false;
            }
        }
    }
</script>
